// The directive tells the Next.js runtime that this code should only be executed on the server.
'use server';

/**
 * @fileOverview This file defines a Genkit flow for generating profile pictures based on a user's name.
 *
 * - generateProfilePicture - A function that takes a user's name and generates a profile picture.
 * - GenerateProfilePictureInput - The input type for the generateProfilePicture function, which includes the user's name.
 * - GenerateProfilePictureOutput - The return type for the generateProfilePicture function, which includes the generated image as a data URI.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

// Define the input schema for the generateProfilePicture function.
const GenerateProfilePictureInputSchema = z.object({
  name: z
    .string()
    .describe('The name of the user for whom to generate a profile picture.'),
});
export type GenerateProfilePictureInput = z.infer<typeof GenerateProfilePictureInputSchema>;

// Define the output schema for the generateProfilePicture function.
const GenerateProfilePictureOutputSchema = z.object({
  profilePicture: z
    .string()
    .describe(
      'The generated profile picture as a data URI that must include a MIME type and use Base64 encoding. Expected format: \'data:<mimetype>;base64,<encoded_data>\'.' // as a data URI
    ),
});
export type GenerateProfilePictureOutput = z.infer<typeof GenerateProfilePictureOutputSchema>;

// Define the main function that will be called to generate the profile picture.
export async function generateProfilePicture(
  input: GenerateProfilePictureInput
): Promise<GenerateProfilePictureOutput> {
  return generateProfilePictureFlow(input);
}

// Define the prompt that will be used to generate the profile picture.
const generateProfilePicturePrompt = ai.definePrompt({
  name: 'generateProfilePicturePrompt',
  input: {schema: GenerateProfilePictureInputSchema},
  output: {schema: GenerateProfilePictureOutputSchema},
  prompt: `Generate a professional profile picture for a person named {{name}}. The image should be suitable for a contact card application, ensuring a clear and friendly appearance.`,
  config: {
    safetySettings: [
      {
        category: 'HARM_CATEGORY_HATE_SPEECH',
        threshold: 'BLOCK_ONLY_HIGH',
      },
      {
        category: 'HARM_CATEGORY_DANGEROUS_CONTENT',
        threshold: 'BLOCK_NONE',
      },
      {
        category: 'HARM_CATEGORY_HARASSMENT',
        threshold: 'BLOCK_MEDIUM_AND_ABOVE',
      },
      {
        category: 'HARM_CATEGORY_SEXUALLY_EXPLICIT',
        threshold: 'BLOCK_LOW_AND_ABOVE',
      },
    ],
  },
});

// Define the Genkit flow for generating the profile picture.
const generateProfilePictureFlow = ai.defineFlow(
  {
    name: 'generateProfilePictureFlow',
    inputSchema: GenerateProfilePictureInputSchema,
    outputSchema: GenerateProfilePictureOutputSchema,
  },
  async input => {
    // Call the image generation model to generate the profile picture.
    const {media} = await ai.generate({
      // IMPORTANT: ONLY the googleai/gemini-2.0-flash-preview-image-generation model is able to generate images. You MUST use exactly this model to generate images.
      model: 'googleai/gemini-2.0-flash-preview-image-generation',
      prompt: `Generate a professional profile picture for a person named ${input.name}. The image should be suitable for a contact card application, ensuring a clear and friendly appearance.`, // Using template literals here because the prompt object doesn't pick up the name properly.
      config: {
        responseModalities: ['TEXT', 'IMAGE'], // MUST provide both TEXT and IMAGE, IMAGE only won't work
      },
    });

    if (!media || !media.url) {
      throw new Error('Failed to generate profile picture.');
    }

    // Return the generated profile picture as a data URI.
    return {profilePicture: media.url};
  }
);
